import Vue from 'vue'
import Router from 'vue-router'
import Home from 'src/views/home/index'



Vue.use(Router)

export default new Router({
  linkActiveClass:"is-active",
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Home
    },
    {
      path: '/detail',
      name: 'detail',
      component:resolve =>{
        require(['src/views/detail/index'],resolve)
      }
    }
  ]
})
